//get header to authentification
export const getHeader = function () {
  const tokenData = JSON.parse(localStorage.getItem('authUser'))
  const headers = {
    'Accept': 'application/json',
    'Authorization': 'Bearer '+ tokenData.access_token 
  }
  return headers
}



