<template lang="html">
  <div class="ui one column stackable center aligned page grid">
   <div class="column twelve wide">
     <div class="ui fluid piled segment left aligned" style="min-height:350px;">
       <div class="ui teal dividing header">
         <i class="user icon"></i>
         <div class="content">
           Se connecter <div class="sub header">veuillez entrez vos informations pour vous connecté</div>
         </div>
       </div>
       <div class="ui active dimmer" v-if="sending">
         <div class="ui loader">

         </div>
       </div>
       <div class="ui one column stackable center fluid aligned page grid active " style="padding-top:20px;">
         <form class="six fuild column wide ui form">
            <div class="field">
              <label>Email</label>
              <input type="text" v-model="login.email" placeholder="votre adress mail">
            </div>
            <div class="field">
              <label>Mot de passe</label>
              <input type="password" v-model="login.password" placeholder="votre mot de passe">
            </div>
            <div class="ui center aligned">
              <button class="ui primary button" @click.prevent="logged" type="submit">Se connecté</button>
            </div>
          </form>
          <div class="ui six fuild column wide error message" style="margin-bottom:20px;" v-if="error">
           <p>Parametre de connexion invalide</p>
          </div>
       </div>
     </div>
   </div>
</div>
</template>

<script>
import {loginUrl, userUrl, getHeader} from '../../config'
import {client_id, client_secret} from '../../env'
import vuex from 'vuex'

import toastr from '../../assets/toastr.min.js'
var localStorage = require('web-storage')().localStorage

export default {
  data() {
    return {
      login:{
        email: "wambakevin7@gmail.com",
        password: "password"
      },
      error: false,
      sending: false
    };
  },
  created () {
    this.$store.dispatch('setLoginState',true)
  },
  computed: {
    ...vuex.mapState(['userStore'])
  },
  ready() {},
  attached() {},
  methods: {
    logged(){
      this.sending = true
      toastr.options = {"newestOnTop": true,"positionClass": "toast-bottom-right"}
      const postData = {
        grant_type: 'password',
        client_id: client_id,
        client_secret: client_secret,
        username: this.login.email,
        password: this.login.password,
        scope: ''
      }
      this.$http.post(loginUrl,postData).then(response => {
        this.error = false
        console.log(response);
        console.log('ok');
        if (response.status == 200){
          const authUser = {}
          authUser.access_token = response.data.access_token
          authUser.refresh_token = response.data.refresh_token
          localStorage.set('authUser', authUser)
          this.$http.get(userUrl,{headers: getHeader()}).then(data => {
              authUser.email = data.body.email
              authUser.name = data.body.name
              localStorage.remove('authUser')
              localStorage.set('authUser', authUser)
              this.$store.dispatch('setLoginState',false)
              this.$router.push({name: 'root'})
          })
        }
        this.sending = false
      }, (response) => {
        console.log(response);
        console.log('non');
        this.error = true
        this.sending = false
      })
    }
  },
  components: {}
};
</script>

<style lang="css">
</style>



